
import { mapActions, mapState } from 'pinia';
import { defineComponent, ref } from 'vue';
import useWindowManager from '../stores/windowManager';
import VWindow from './VWindow.vue';
import { IIcon, PIDType } from '../scripts/types';
import { ComponentType } from '../windowApp';
import useIcons from '../stores/icons';

type RDEventType = 'start' | 'end' | 'move';

interface WindowResizeEventType {
    type: RDEventType;
    width: number;
    height: number;
}

interface WindowDragEventType {
    type: RDEventType;
    top: number;
    left: number;
}

export default defineComponent({
    name: 'TheWindows',
    components: {
        VWindow,
    },
    setup() {
        return {
            color: ['green', 'pink'],
            toBeClosed: [] as PIDType[],
        };
    },
    computed: {
        ...mapState(useWindowManager, ['renderOrder', 'process', '_window']),
    },
    methods: {
        ...mapActions(useWindowManager, [
            'initWindowManager',
            'makeWindow',
            'focusOn',
            'closeWindow',
            'setToFullScreen',
            'restoreWindowShape',
            'updateShape',
        ]),
        handleResize(
            pid: PIDType,
            { width, height, type }: WindowResizeEventType,
        ): void {
            if (type === 'start') this.focusOn('window', pid);
            this.updateShape(pid, width, height);
        },
        handleDrag(
            pid: PIDType,
            { top, left, type }: WindowDragEventType,
        ): void {
            if (type === 'start') this.focusOn('window', pid);
            const { position } = this._window(pid);
            position.x = left;
            position.y = top;
        },
        getWindowIcon(compType: string): IIcon {
            return useIcons().compIcon(compType as ComponentType);
        },
    },
    mounted() {
        this.$nextTick(() => {
            // this.makeWindow({
